  box-shadow: inset 0 0 0 4px var(--b-color, white);
} */

:root{
  --gap: 1rem;
}

body{
  background-color: black;
  color: white;
}

main{
  color: var(--1st-color);

  justify-content: center;
  align-items: center;
  flex-direction: column;
  overflow-y: hidden;

  height: 100dvh;
}

label{
  display: flex;
  align-items: center;
}

.tabs label > span{ /* temporal */
  display: none;
  text-transform: uppercase;
  font-weight: bold;

  background-color: black;
  color: white;

  font-size: 0.9rem;
  border-radius: 5px;
  padding: 0.4rem 0.4rem;
}

label:hover{
  cursor: pointer;
}

/* label:hover{ cursor: pointer; } */

.light-mode{
  --1st-color: black;
  --2nd-color: white;
}

.dark-mode{
  --1st-color: white;
  --2nd-color: black;
}

.icon{
  font-size: larger;
  margin: 0.3rem;
}

.button{
  width: fit-content;
}

.button:hover, label:hover > .icon{
  cursor: pointer;
  transform: scale(1.3);
}

.button:active, label:active > .icon{
  cursor: pointer;
  transform: scale(1.15);
}

.button.youtube{
  background-color: red;
  font-size: x-small;
  border-radius: 5px;
  padding: 0.1rem 0.2rem;
}

.header{
  display: none !important;
  top: var(--gap);
  right: var(--gap);
  /* right: calc(var(--gap) * 1.5); */
}

.tabs{
  color: gray;
  flex-direction: column;
  left: var(--gap);
}

.tabs:hover{
  /* background-color: black;
  box-shadow: 0 0 10px 15px black; */
}

.tabs:hover label > span{
  /* display: flex; */
}

.tabs label > :first-child:hover + span{
  display: flex;
}

.vert{ /* a */
  flex-direction: column;
  overflow-y: scroll;
  overflow-x: hidden;
  scroll-snap-type: y mandatory;
  height: 100%;
  width: 100%;

  /* Edge, Firefox */
}

.vert::-webkit-scrollbar{
  display: none;
}

.page{ --b-color: darkblue;
  scroll-snap-align: center;
  min-height: 100vh;
  height: 100vh;
  /* background-color: lightblue; */
}

.page.welcome{ --b-color: lightsalmon;
  position: relative;
  background-color: var(--2nd-color);
  justify-content: center;
  align-items: center;
  flex-direction: column;
}

.welcome > img{
  width: 10rem;
  height: 10rem;
  border-radius: 100%;
  object-fit: cover;
  /* background-color: lightgreen; */
}

.welcome > *{
  margin: 1.25rem 0;
}

.profile{
  display: flex;
  text-transform: uppercase;
  font-weight: bold;
}

.profile > span{
  transform: rotate(90deg);
  font-size: 2rem;
  line-height: 2rem;
  color: yellow;
}

.name{
  flex-direction: column;
  /* align-items: center; */
  color: goldenrod;
  /* margin: 1rem; */
  font-size: 2rem;
  line-height: 2.25rem;
}

.name > span{
  display: flex;
  /* transform: translateY(-10px); */
  font-size: xxx-large;
}

.intro{
  text-align: center;
  font-size: large;
  width: 70%;
}

.page.list{ --b-color: lightsalmon;
  /* background: rgba(1,1,1,0.3); */
  /* background: rgba(1,1,1,0.5); */
  background: linear-gradient(127deg, #000000c7, rgba(1,1,1,0.25));
  position: relative;
  justify-content: space-between;
  align-items: center;
}

.page.list > :not(.video, .menu){
  height: fit-content;
}

.video{ --b-color: lightgreen;
  background: rgba(255, 255, 255, 0.05);
  width: 100%;
  height: 100%;
  z-index: -1 !important;
}

.video > iframe{
  height: 100vh;
  width: 115vh;
}

.front-page{
  width: 100%;
  height: 100%;
  background-color: black;
}

.controls{
  display: none !important;
  flex-direction: column-reverse;
  bottom: var(--gap);
  left: var(--gap);
}

.info{
  width: 70%;
  /* padding-left: calc( var(--gap) * 2 + 2rem ); */
  /* padding-left: 4rem; */
  padding-left: 4.5rem;
  /* background: url('./res/brush-bg.png'); */
  background-repeat: no-repeat;
  background-size: contain;
}

.preview{
  flex-direction: column;
  justify-content: end;
  font-size: large;
  writing-mode: vertical-lr;
  transform: rotate(180deg);
  /* background: white; */
  /* height: 100vh; */
}

.preview .center,
.p-content{
  /* margin-right: 0.5rem; */
  cursor: default;
  flex-direction: column;
  align-items: center;
  text-transform: uppercase;
  font-weight: bold;
}

/* .preview .center > span,
.p-content > span{
  color: black;
  width: 1rem;
  height: 1rem;

  border-radius: 100%;
} */

.preview .end{
  justify-content: end;
  align-items: end;
}

.start, .center, .end{
  height: 33dvh;
  /* background-color: gray; */
}

.footer{
  bottom: var(--gap);
  right: var(--gap);
}

/* MENU */

.menu{ --b-color: violet;
  /* background-color: var(--b-color); */
  transition: transform 0.2s 0.2s;
  transform: translateX(100%);
  height: 100%;
  right: 0;
}

.border{
  --b-color: darkviolet;
}

/* https://codepen.io/sosuke/pen/Pjoqqp */

.border img{
  filter: 
    invert(7%) 
    sepia(1%) 
    saturate(144%) 
    hue-rotate(314deg) 
    brightness(90%) 
    contrast(99%);
  height: 100%;
}

.menu-places{ --b-color: #111;
  background-color: var(--b-color);
  flex-direction: column;
  justify-content: center;
  align-items: center;
  /* color: black; */
  /* font-weight: bold; */
  padding: 1rem;
  padding-right: 2rem;
}

.place-jobs{
  flex-direction: column;
  align-items: center;
  margin: 1.25rem 0;
}

.poppins-regular, .side > span, .preview, .profile{
  font-family: "Poppins", sans-serif;
  font-weight: 900;
  font-style: normal;
  letter-spacing: 1px;
}

.warning{
  position: absolute;
  text-align: center;
  display: flex;
  flex-direction: column;
  align-items: center;

  /* background: lightcoral; */
  background-color: khaki;
  color: black;

  padding: 0.5rem;
  margin: 1rem;
  bottom: 0;
}

.warning .close{
  width: fit-content;
  text-decoration: underline;
  font-weight: bold;
  cursor: pointer;
  color: blue;
}

[name="items"] + label,
[name="places"] + label > span{
  margin: 0.1rem 0;
}

[name="tabs"],
[name="tabs"]:disabled + label,
[name="items"],
[name="items"]:disabled + label,
[name="places"],
[name="places"]:disabled + label,
[name="controls"],
[name="controls"]:disabled + label{
  display: none;
}

[name="tabs"]:checked + label{
/* [name="items"]:checked + label, */
/* [name="places"]:checked + label .place-title{ */
  /* background-color: gray; */
  color: var(--1st-color);
}

[name="items"]:checked + label{
  font-weight: bold;
}

[name="controls"] + label{
  display: block;
}

[name="controls"]:checked + label{
  display: none;
}


/* MIX */

main,
.header, .tabs, .vert, 
.welcome, .name, 
.page.list, .controls, 
.p-content, .menu, 
.list, .place-jobs, .preview, .center, .end{
  display: flex;
}

.header, .tabs, .footer, .video, .controls, .menu{
  position: absolute;
  z-index: 1;
}

.footer:hover ~ .menu, .preview:hover ~ .menu, .menu:hover{
  transform: translateX(0);
}

/* .ytp-pause-overlay-container */

@media(min-width: 640px){ .info{ width: 50%; } }

@media(min-width: 1024px){
  .info{ width: 30%; } 
  .intro{ width: 45%; } 
}