/**/

.cancel{
  [name="places"], 
  [name="places"]:disabled + label{
    display: none;
  }
  
  [name="places"] + label{
    /* text-align: center; */
    width: 100%;
  }
  
  [name="places"] + label > span{ /* places */
    text-transform: uppercase;
    display: flex;
    justify-content: center;
    font-weight: bold;
  }
  
  [name="places"] + label:hover > span{ 
    background: darkviolet;
  }
  
  [name="places"]:checked + label > span{ /* places checked */
    text-decoration: underline;
  }
  
  [name="places"]:checked + label > .jobs{ /* jobs div */
    display: flex;
  }
  
  label > .jobs{ /* jobs div */
    display: none;
    flex-direction: column;
  }
}

.menu{
  --b-color: none;
  color: white;
  /* background-color: #111; */
}

.menu-places{
  text-transform: uppercase;
}

.menu label{
  cursor: pointer;
}

[name="places"] + label > span{
  color: lightcoral; 
}

[name="places"] + label > span:hover{
  color: white; 
}

/* .place-jobs:has(:checked){
  background: blue;
} */

.preview, .bubble, .preview > span{
  display: flex;
}

.bubble{
  background: lightgreen;
  color: black;
  justify-content: center;
  align-items: center;
  font-weight: bold;

  padding: 0.4rem;
  width: 1rem;
  height: 1rem;

  border-radius: 100%;
}

.bubble:before{
  content: 'ALL';
}

.ytp-pause-overlay-container{
  display: none;
}

.front-page > img{
  object-fit: cover;
  object-position: right;
  width: 100vw;
  height: 100vh;
}

.controls .button > img{
  width: 1.5rem;
  margin-left: 0.4rem;
}

.side.title > span{
  font-size: x-large;
  /* color: rgb(224, 197, 40); */
}

.side.title{
  font-size: large;
}

.side.title li{
  display: flex;
  /* width: 50%; */
}

li, ul{ all: unset; }